/**
 * DataObject which represents a text style (cf {@link Text}).
 */
export default class TextStyle {
    constructor() {
        /** @type {String} */
        this.fontFamily = "Arial";
        /** @type {Number} */
        this.fontSize = 12;
        /** @type {color} */
        this.color = "#000000";
        /** @type {Boolean} */
        this.bold = false;
        /** @type {Boolean} */
        this.italic = false;
        /**
         * Horizontal text align.
         * @type {String} values can be "left", "center", "right", "justify"  (justify for DOM only).
         */
        this.textAlign = "left";
        /**
         * How overflow of text outside width/height bounds is managed (DOM Only).
         * @type {String} values can be "left", "auto", visible", "hidden", "scroll".
         */
        this.overflow = "auto";
    }
}
